import dayjs from 'dayjs';
export const doc = document.documentElement;
export function getVarStyle(name, el = doc) {
    return getComputedStyle(el).getPropertyValue(name);
}
export function setVarStyle(key, value, el = doc) {
    el.style.setProperty(key, value);
}
export function setVarStyleByConfig(config, el = doc) {
    for (const varName in config) {
        const varValue = config[varName];
        setVarStyle(varName, varValue, el);
    }
}
export function clearStyle(el = doc) {
    el.setAttribute('style', '');
}
export function getComponentStyle(stylesItem = { id: '', styles: [] }) {
    const attrsList = [];
    stylesItem.styles.map(style => {
        const varName = style;
        const varValue = getVarStyle(style);
        const obj = {
            varName,
            varValue
        };
        if (varValue.trim().startsWith('#') || varValue.trim().startsWith('rgba')) {
            obj.component = 'n-color-picker';
        }
        else {
            obj.component = 'n-input';
            obj.suffix = 'px';
        }
        attrsList.push(obj);
    });
    return attrsList;
}
export function getTime() {
    return dayjs().valueOf();
}
export function transitionTime(time) {
    return dayjs(time).format('YYYY-MM-DD HH:mm');
}
export function humpToScribe(target) {
    return target.replace(/([A-Z])/g, function (match, p, offset) {
        const lower = match.toLowerCase();
        return offset !== 0 ? '-' + lower : lower;
    });
}
export function scribeToHump(target, searchValue = '--van-', replaceValue = '') {
    const newValue = target.replace(searchValue, replaceValue);
    return newValue.replace(/-(\w)/g, function (all, letter) {
        return letter.toUpperCase();
    });
}
